<template>
  <div>
    <div class="goods">
      <div class="menu-wrapper" ref="menu-wrapper">
      	<ul>
      		<li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="selectMenu(index,$event)">
      			<span class="text border-1px"><span class="icon" v-show="item.type>0" :class="classMap[item.type]">
      			</span>{{item.name}}</span>	
      		</li>
      	</ul>
      </div>
      <div class="foods-wrapper" ref="foods-wrapper">
      	<ul>
      		<li v-for="item in goods" class="food-list food-list-hook">
      			<h1 class="title">{{item.name}}</h1>
      			<ul>
      				<li @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item">
      					<div class="icon">
      						<img width="57" height="57" :src="food.icon" alt="">
      					</div>
      					<div class="content">
      						<h2 class="name">{{food.name}}</h2>
      						<p class="desc">{{food.decription}}</p>
      						<div class="extra">
      							<span class="count">月售{{food.sellCount}}份</span>
      							<span>好评率{{food.rating}}%</span>
      						</div>
      						<div class="price">
      							<span class="now">￥{{food.price}}</span>
      							<span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
      						</div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food" @increment="incrementTotal"></cartcontrol>
                  </div>
      					</div>
      				</li>
      			</ul>
      		</li>
      	</ul>
      </div>
      <shopcart ref="shopCart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"  @increment="incrementTotal"></shopcart>
    </div>
    <food :food="selectedFood" ref="food" @increment="incrementTotal"></food>
  </div>
</template>

<script  type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import shopcart from '../../components/shopcart/shopcart';
  import cartcontrol from '../../components/cartcontrol/cartcontrol';
  import food from '../../components/food/food';
  // const ERR_OK = 0;
  export default {
    props: {
        seller: {
           type: Object
        }
    },
    data () {
        return {
           goods: [],
           listHeight: [],
           scrollY: 0,
           selectedFood: {}
        };
    },
    created () {
        this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
        // this.$http.get('api/goods').then((response) => {
        //     response = response.body;
        //     if (response.errno === ERR_OK) {
        //         this.goods = response.data;
        //         this.$nextTick(() => {
        //             this._initScroll();
        //             this._calculateHeight();
        //         });
        //     }
        // });
        let appData = require('../../../data.json');
        this.goods = appData.goods;
        this.$nextTick(() => {
          this._initScroll();
          this._calculateHeight();
        });
    },
    computed: {
      currentIndex: function () {
        for (let i = 0; i < this.listHeight.length; i++) {
          var height1 = this.listHeight[i];
          var height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY > height1 && this.scrollY < height2)) {
            return i;
          }
        }
        return 0;
      },
      selectFoods () {
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
    methods: {
        _initScroll () {
            this.menuScroll = new BScroll(this.$refs['menu-wrapper'], {
              click: true
            });
            this.foodScroll = new BScroll(this.$refs['foods-wrapper'], {
              click: true,
              probeType: 3
            });
            this.foodScroll.on('scroll', (pos) => {
              this.scrollY = Math.abs(Math.round(pos.y));
            });
        },

        _calculateHeight () {
          let foodList = this.$refs['foods-wrapper'].getElementsByClassName('food-list-hook');
          let height = 0;
          this.listHeight.push(height);
          for (let i = 0; i < foodList.length; i++) {
            let item = foodList[i];
            height += item.clientHeight;
            this.listHeight.push(height);
          }
        },
        selectMenu (index, event) {
          if (!event._constructed) {
            return;
          }
          let foodList = this.$refs['foods-wrapper'].getElementsByClassName('food-list-hook');
          let el = foodList[index];
          this.foodScroll.scrollToElement(el, 300);
        },
        selectFood (food, event) {
          if (!event._constructed) {
            return;
          }
          this.selectedFood = food;
          this.$refs.food.show();
        },
        incrementTotal (target) {
          this.$refs.shopCart.drop(target);
        }
    },
    components: {
      shopcart,
      cartcontrol,
      food
    }
  };
</script>

<style lang="stylus" type="text/stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/index";
  .goods
  	display flex
  	position absolute
  	width 100%
  	top 174px
  	bottom 46px
  	overflow hidden
  	.menu-wrapper
  		flex 0 0 80px
  		width 80px
  		background #f3f5f7
  		.menu-item
        display table 
        height 54px
        width 56px
        line-height 14px
        padding 0 12px
        margin auto   
        &.current
          position relative
          z-index 10
          margin-top -1px
          background #fff
          font-weight 700
          .text
            border-none()   
  			.icon
  				display inline-block
  				vertical-align top
  				width 12px
  				height 12px
  				margin-right 4px
  				background-size 12px 12px
  				background-repeat no-repeat
  				&.decrease
  					bg-image('decrease_3')
  				&.discount
  					bg-image('discount_3')
  				&.guarantee
  					bg-image('guarantee_3')
  				&.invoice
  					bg-image('invoice_3')
  				&.special
  					bg-image('special_3')
  			.text
  				display table-cell
  				width 56px
  				vertical-align middle
  				border-1px(rgba(7,17,27,0.1))
  				font-size 12px
  	.foods-wrapper
  		flex 1
  		.title
  			padding-left 14px
  			height 26px
  			line-height 26px
  			border-left 1px solid #d9dde1
  			font-size 12px
  			color rgb(147,153,159)
  			background #f3f5f7
  		.food-item
  			display flex
  			margin 18px
  			padding-bottom 18px
  			border-1px(rgba(7,17,27,.1))
  			&:last-child
  				border-none()
  				margin-bottom 0
        &.current
          position relative
          margin-top -1px
          background #fff
          font-weight 700 
  			.icon
  				flex 0 0 57px
  				margin-right 10px
  			.content
  				flex 1
  				.name
  					margin 2px 0 8px 0
  					height 14px
  					line-height 14px
  					font-size 14px
  					color rgb(7,17,27)
  				.desc,.extra
  					line-height 10px
  					font-size 10px
  					color rgb(147,153,159)
  				.desc
            line-height 12px    
  					margin-bottom 8px
  				.extra
  					line-height 10px
  					font-size 10px
  					.count
  						margin-right 12px
  				.price
  					font-weight 700
  					line-height 24px
  					.now
  						margin-right 8px
  						font-size 14px
  						color rgb(240,20,20)
					.old
						text-decoration line-through
						font-size 10px
						color rgb(147,153,159)
</style>



