<template>
	<div class="seller" ref="seller">
		<div class="seller-content">
			<div class="overview">
				<h1 class="title">{{seller.name}}</h1>
				<div class="desc border-1px">
					<star :size="36" :score="seller.score"></star>
					<span class="text">({{seller.ratingCount}})</span>
					<span class="text">月售{{seller.sellCount}}单</span>
				</div>
				<ul class="remark">
					<li class="block">
						<h2>起送价</h2>
						<div class="content">
							<span class="stress">{{seller.minPrice}}</span>元
						</div>
					</li>
					<li class="block">
						<h2>商家配送</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryTime}}</span>元
						</div>
					</li>
					<li class="block">
						<h2>平均配送时间</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryTime}}</span>元
						</div>
					</li>
				</ul>
        <div class="favorite" @click="toggle">
          <span class="icon-favorite" :class="{'active':favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
			</div>
			<split></split>
			<div class="bulletin">
				<h1 class="title">公告与活动</h1>
				<div class="content-wrapper">
					<p class="content">{{seller.bulletin}}</p>
				</div>
				<ul v-if="seller.supports" class="supports">
	              <li class="support-item border-1px" v-for="(item,index) in seller.supports">
	                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
	                <span class="text">{{seller.supports[index].description}}</span>
	              </li>
	            </ul>
			</div>
			<split></split>
			<div class="pic">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref="pic-wrapper">
					<ul class="pic-list" ref="pic-list">
						<li class="pic-item" v-for="pic in seller.pics">
							<img :src="pic" width="120" height="120">
						</li>
					</ul>
				</div>
			</div>
			<split></split>
			<div class="info">
				<h1 class="title">商家信息</h1>
				<ul>
					<li class="info-item" v-for="info in seller.infos">{{info}}</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
import star from '../../components/star/star';
import split from '../../components/split/split';
export default {
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      favorite: false
    };
  },
  computed: {
    favoriteText () {
      return this.favorite ? '已收藏' : '收藏';
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    this.$nextTick(() => {
      this._initScroll();
      this._initPicScroll();
    });
  },
  watch: {
    'seller' () {
      this._initScroll();
      console.log('initScroll');
      this._initPicScroll();
      console.log('initPic');
    }
  },
  ready () {
    this._initScroll();
    this._initPicScroll();
    console.log(this.$refs['pic-list'].style.width);
  },
  methods: {
    toggleFavorite (event) {
      console.log('toggleFavoriteBefore');
      if (!event._constructed) {
        return;
      }
      this.favorite = !this.favorite;
      console.log('toggleFavorite');
    },
    toggle () {
      console.log('clicked');
      this.favorite = !this.favorite;
    },
    _initScroll () {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs['seller'], {
          click: true,
          hasVerticalScroll: true
        });
      } else {
        this.scroll.refresh();
      }
    },
    _initPicScroll () {
      if (this.seller.pics) {
        let picWidth = 120;
        let margin = 6;
        let width = (picWidth + margin) * this.seller.pics.length;
        this.$refs['pic-list'].style.width = width + 'px';
        console.log(this.$refs['pic-list'].style.width);
        if (!this.picScroll) {
          this.picScroll = new BScroll(this.$refs['pic-wrapper'], {
            scrollX: true,
            eventPassthrough: 'vertical'
          });
        } else {
          this.picScroll.refresh();
        }
      }
    }
  },
  components: {
    star,
    split
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixim";
  .seller
  	position absolute
  	top 174px
  	bottom 0
  	left 0
  	width 100%
  	overflow hidden
  	.seller-wrapper
  		height 100%
  		overflow hidden
  	.overview
  		padding 18px
  		.title
  			margin-bottom 8px
  			line-height 14px
  			color rgb(7,17,27)
  			font-size 14px
  		.desc
  			padding-bottom 18px
  			border-1px(rgba(7,17,27,0.1))
  			font-size 0
  			.star
  				display: inline-block
  				margin-right 8px
  				vertical-align top
  			.text
  				display inline-block
  				margin-right 12px
  				line-height 18px
  				vertical-align top 
  				font-size 10px
  		.remark
  			display flex
  			padding-top 18px
  			.block
  				flex 1
  				text-align center
  				border-right 1px solid rgba(7,17,27,0.1)
  				&:last-child
  					border none
  				h2
  					margin-bottom 4px
  					line-height 10px
  					font-size 10px
  					color rgb(147,153,159)
  				.content
  					line-height 24px
  					font-size 10px
  					color rgb(7,17,27)
  					.stress
  						font-size 24px
    .favorite
      position absolute
      right 11px
      top 18px
      width 50px
      text-align center
      .icon-favorite
        display block
        margin-bottom 4px
        line-height 24px 
        font-size 24px   
        color #d4d6d9
        &.active
          color rgb(240,20,20)   
      .text
        line-height 10px
        font-size 10px
        color rgb(77,85,23) 
  	.bulletin
  		padding 18px 18px 0 18px
  		.title
  			margin-bottom 8px
  			line-height 14px
  			color rgb(7,17,27)
  			font-size 14px
  		.content-wrapper
  			padding 0 12px 16px 12px
  			border-1px(rgba(7,17,27,0.1))
  			.content
  				line-height 24px
  				color rgb(240,20,20)
  				font-size 12px
			.supports
				.support-item
					padding 16px 12px
					border-1px(rgba(7,17,27,0.1))
					font-size 0
					.icon 
			            display inline-block
			            vertical-align top
			            width 16px
			            height 16px 
			            margin-right 6px
			            background-size 16px 16px
			            background-repeat no-repeat
			            &.decrease
			              bg-image('decrease_4')
			            &.discount
			              bg-image('discount_4')
			            &.guarantee
			              bg-image('guarantee_4')
			            &.invoice
			              bg-image('invoice_4')
			            &.special
			              bg-image('special_4')
					.text
						line-height 16px
						font-size 12px
						color rgb(7,17,27)
				&:last-child
					border none
  .info
    padding 18px 18px 0 18px
    color rgb(7,17,27)
    .title
      padding-bottom 18px
      line-height 14px
      border-1px(rgba(7,17,27,0.1))  
      font-size 14px
    .info-item
      padding 16px 12px
      border-1px(rgba(7,17,27,0.1))  
      &:last-child
        border-none()
	.pic
		padding 18px
		.title
			margin-bottom 8px
			line-height 14px
			color rgb(7,17,27)
			font-size 14px
		.pic-wrapper
			width 100%
			overflow hidden
			white-space nowrap
			.pic-list
				font-size 0
				.pic-item
					display inline-block
					margin-right 6px
					width 120px
					height 90px
					&:last-child
						margin 0      
          
</style>



